body {
  background: #fff;
  font-family: "Roboto", sans-serif;
}

h2 {
  font-weight: 700;
  font-size: 36px;
  line-height: 1.2;
  color: #201b1b;
}

.btn__yellow {
  padding: 12px 24px;
  border: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.3;
  text-align: center;
  color: #201b1b;
  transition: all 0.3s;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
  background: #fac800;
}
.btn__yellow:hover {
  background: #ffde5c;
}
.btn__gray {
  border: none;
  padding: 12px 24px;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.3;
  text-align: center;
  color: #201b1b;
  transition: all 0.3s;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
  background: #ededed;
}
.btn__gray:hover {
  background: #e3e3e3;
}
.btn__light {
  padding: 12px 24px;
  border: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.3;
  text-align: center;
  color: #201b1b;
  transition: all 0.3s;
  border-radius: 4px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
  background: #fff6d1;
}
.btn__light:hover {
  background: #ffefaf;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 16px;
  box-sizing: content-box;
}

.nav {
  padding: 17px 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.nav__logo {
  width: 100%;
  max-width: 55px;
  display: block;
}
.nav__logo img {
  width: 100%;
}
.nav__phone {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #fff;
  transition: all 0.3s;
  margin: 0 16px 0 auto;
}
.nav__phone svg path {
  transition: all 0.3s;
}
.nav__phone:hover {
  color: #ffde5c;
}
.nav__phone:hover svg path {
  fill: #ffde5c;
}

.header {
  padding: 0 0 160px;
  background: url("../img/header_bg.jpg") right no-repeat;
  background-size: cover;
}
.header__info {
  max-width: 515px;
  margin: 83px 0 0;
}
.header h1 {
  font-weight: 600;
  font-size: 64px;
  line-height: 1;
  letter-spacing: -0.02em;
  color: #fff;
}
.header h1 span {
  color: #fac800;
}
.header__list {
  margin: 36px 0 0;
  display: grid;
  gap: 13px;
}
.header__list li {
  display: grid;
  grid-template-columns: 81px 1fr;
  gap: 20px;
  align-items: center;
}
.header__list p {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  color: #fff;
}
.header__img {
  border-radius: 100%;
  width: 81px;
  height: 81px;
  border: 5px solid rgba(255, 255, 255, 0.32);
  position: relative;
}
.header__img img {
  width: 71px;
  height: 71px;
  object-fit: contain;
  position: relative;
}
.header__row {
  margin: 36px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
.header__row button {
  width: 100%;
}

.advant {
  padding: 100px 0;
  position: relative;
  overflow: hidden;
}
.advant h2 {
  max-width: 500px;
}
.advant__title {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.advant__arrow {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.advant__arrow .swiper-button-prev,
.advant__arrow .swiper-button-next {
  position: relative;
  inset: auto;
  transform: none;
  margin: 0;
  width: 40px;
  height: 40px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
  background: #fac800;
  transition: all 0.3s;
  border-radius: 100%;
}
.advant__arrow .swiper-button-prev::after,
.advant__arrow .swiper-button-next::after {
  content: none;
}
.advant__arrow .swiper-button-prev svg,
.advant__arrow .swiper-button-next svg {
  width: 20px;
  height: 20px;
}
.advant__arrow .swiper-button-prev:hover,
.advant__arrow .swiper-button-next:hover {
  background: #ffde5c;
}
.advant__arrow .swiper-button-prev.swiper-button-disabled,
.advant__arrow .swiper-button-next.swiper-button-disabled {
  opacity: 0.6;
  background: #f2f2f2;
}
.advant__slider {
  margin: 40px 0 0;
  overflow: visible !important;
}
.advant .swiper-wrapper {
  overflow: visible;
}
.advant .swiper-pagination {
  display: none;
}
.advant__img {
  position: relative;
}
.advant__img img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 12px;
}
.advant__img span {
  position: absolute;
  top: 12px;
  left: 12px;
  border-radius: 8px;
  background: #fff;
  padding: 10px;
  display: block;
}
.advant__img span svg {
  width: 20px;
  height: 20px;
}
.advant__slide h3 {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  color: #201b1b;
  margin: 12px 0 0;
}
.advant__slide p {
  margin: 12px 0 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #201b1b;
}

.stock {
  padding: 60px 0;
  background: #f2f2f2;
}
.stock__wrapper {
  margin: 40px 0 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}
.stock__item {
  border-radius: 16px;
  padding: 24px 30px;
  box-shadow: 0 2px 15px -2px rgba(0, 0, 0, 0.12);
  background: #fff;
  display: grid;
  grid-template-columns: calc(50% - 15px) calc(50% - 15px);
  gap: 30px;
}
.stock__img {
  border-radius: 12px;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.stock__img_slide {
  border-radius: 12px;
  overflow: hidden;
  position: relative;
}
.stock__img_slide img {
  width: 100%;
  min-height: 313px;
  object-fit: cover;
}
.stock__img_val {
  position: absolute;
  top: 16px;
  left: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 4px 12px;
  border-radius: 99px;
  box-shadow: 0 2px 4px -2px rgba(0, 0, 0, 0.13);
  background: #fff;
  z-index: 5;
}
.stock__img_val p {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #201b1b;
}
.stock .swiper-button-prev,
.stock .swiper-button-next {
  inset: auto;
  margin: 0;
  width: 20px;
  height: 20px;
  transition: all 0.3s;
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
}
.stock .swiper-button-prev::after,
.stock .swiper-button-next::after {
  content: none;
}
.stock .swiper-button-prev svg,
.stock .swiper-button-next svg {
  width: 20px;
  height: 20px;
}
.stock .swiper-button-prev svg path,
.stock .swiper-button-next svg path {
  transition: all 0.3s;
}
.stock .swiper-button-prev:hover path,
.stock .swiper-button-next:hover path {
  fill: #ffde5c;
}
.stock .swiper-button-next {
  left: auto;
  right: 8px;
}
.stock .swiper-pagination {
  position: absolute;
  transform: none;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  inset: auto;
  bottom: 16px;
}
.stock .swiper-pagination span {
  border-radius: 2px;
  width: 30px;
  height: 5px;
  background: #fff;
  opacity: 0.5;
  margin: 0;
  transition: all 0.3s;
}
.stock .swiper-pagination span.swiper-pagination-bullet-active {
  opacity: 1;
}
.stock__info h3 {
  font-weight: 700;
  font-size: 26px;
  line-height: 1.2;
  color: #201b1b;
}
.stock__info ul {
  margin: 16px 0 0;
  display: grid;
  gap: 4px;
}
.stock__info ul li {
  display: block;
}
.stock__info ul li p {
  display: inline;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  color: #565656;
  margin: 0 0 0 8px;
}
.stock__info ul li p + p {
  color: #201b1b;
}
.stock__info .btn__yellow {
  margin: 20px 0 0;
  width: 100%;
}
.stock__btn {
  margin: 8px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  width: 100%;
}